<template>
  <div class="demo-wrapper">
    <r-popup-base
      v-model:show="show"
      closeOnClickOutside
      class="demo-popup-base"
    >
      点击外部关闭
    </r-popup-base>
    <r-button @click="showPopup">弹窗</r-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false
    }
  },
  methods: {
    showPopup() {
      this.show = !this.show
    }
  }
}
</script>
